<template>
  <div id="app">
    <appHeader />
    <upperFold />
    <carousel />

    <orientation />
    <projectThumb />
    <transition name="slide-fade">
    <lightbox
      @pja-lb-close="pjaLbClose"
      @pja-lb-next='pjaLbNext'
      @pja-lb-prev='pjaLbPrev'
      :pj-archives='pjArchives'
      :light-box-show='lightBoxShow'
      :light-box-image='lightBoxImage'
      :light-box-alt='lightBoxAlt'
    />
    </transition>
    <archived
      @pja-lb-open="pjaLbOpen"
      :pj-archives='pjArchives'
    />
    
    <appFooter />
  </div>
</template>

<script>
import { ref } from 'vue';

import appHeader from './components/appHeader.vue'
import appFooter from './components/appFooter.vue'

import upperFold from './components/upperFold.vue'
import carousel from './components/carousel.vue'
import orientation from './components/orientation.vue'
import projectThumb from './components/projectThumb.vue'
import archived from './components/archived.vue'

import lightbox from './components/lightbox.vue'

export default {
  name: 'App',
  components: {
    upperFold, appHeader, appFooter, carousel, orientation, projectThumb, archived, 
    lightbox
  },
  setup(){
    let lightBoxShow = ref(false);
    let lightBoxImage, lightBoxAlt = ref(null);
    let imgIndex = ref(null);
    return{
      pjArchives: [
        {
          thumbnail: require("@/assets/images/thumb_sdpool_corp_branding.jpg"),
          src: require("@/assets/images/sdpool_corp_branding.jpg"),
          alt: "Sdpool Corporate Branding, Design, Frontend Wordpress"
        },

        {
          thumbnail: require("@/assets/images/thumb_fengwangfu_marketing_atl.jpg"),
          src: require("@/assets/images/fengwangfu_marketing_atl.jpg"),
          alt: "FengWangFu Malaysia Corporate Branding, ATL Design, Web Design, Frontend HTML"
        },
        {
          thumbnail: require("@/assets/images/thumb_circle_square_montage.jpg"),
          src: require("@/assets/images/circle_square_montage.jpg"),
          alt: "Circle Square, Web Design, Frontend PHP , Client Management"
        }
      ],
      lightBoxShow,
      lightBoxImage,
      lightBoxAlt,
      imgIndex,
    }
  },
  methods: {
    pjaLbOpen: function(index, img){
      this.lightBoxShow = true;
      this.lightBoxImage = img.src;
      this.lightBoxAlt = img.alt;
      index;
      let nextImg = (this.imgIndex ++) % this.pjArchives.length;
      //console.log(nextImg)
      nextImg;
    },
    pjaLbClose: function(){
      this.lightBoxShow = false;
    },
    pjaLbNext: function(){
      let nextImg = (this.imgIndex ++) % this.pjArchives.length;
      /*console.log(newIndex)
      console.log(this.pjArchives[newIndex].src)*/
      this.lightBoxImage = this.pjArchives[nextImg].src;
      this.lightBoxAlt = this.pjArchives[nextImg].alt;
      //console.log(this.pjArchives[number].src)
      //console.log(this.pjArchives[nextImg].src)
    },
    pjaLbPrev: function(){
      let nextImg = (this.imgIndex --) % this.pjArchives.length;
      this.lightBoxImage = this.pjArchives[nextImg].src;
      this.lightBoxAlt = this.pjArchives[nextImg].alt;
    }
  }
}
</script>

